.item {
    display: flex;
    flex-flow: column nowrap;
    width: 250px;
    height: 450px;
  
  
    &__content {
      display: flex;
      flex-flow: column nowrap;
      align-items: center;
      gap: 0px;
  
      text-decoration: none;
    }
    &__image {
      width: 250px;
      height: 350px;
      object-fit: cover;
    }
  
    &__title {
      width: 100%;
      margin: 0;
      height: 80px;
  
      font-size: 20px;
      
      color: rgb(92, 92, 92);
  
      border: 1px solid #000;
      border-top: 0;
  
      display: flex;
      align-items: center;
      justify-content: center;
  
    }
  }
  